define([
  'angular'
], function(angular) {
    return function(app, elem, attrs, scope) {
        app.factory('headerService', ['$rootScope', '$http', 'G', function($rootScope, $http, G) {
            return {
                loginOut: function() {
                    return $http({
                        url: '/ehuodiBedrockApi/baseconfigcs/logout',
                        method: 'get',
                        test: '/modules/header/header.json'//api
                    });
                },
                selectFbcmslogo: function(opts) {
                    return $http({
                        url: '/goodstaxiAdmin/drumbeatingcs/selectFbcmslogo',
                        method: 'post',
                        data: opts
                    });
                },
                selectWorkThingList: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/workthingcs/selectWorkThingList',
                        method: 'post',
                        data: opts
                    });
                },
                dealMsg: function(opts) {
                    return $http({
                        url: '/ehuodiBedrockApi/workthingcs/dealWorkThing',
                        method: 'post',
                        data: opts
                    });
                }
            };
        }]);
  }
});