            module.directive('phone', function ($timeout) {
                return {
                    restrict: 'A',
                    require: 'ngModel',
                    link: function (scope, ele, attrs, ngModelController) {
                        var parsers=function (value) {
                            if (
                                (value &&
                                    value.length === 11 &&
                                    !value.match(/\D/)) ||
                                !value
                            ) {
                                ngModelController.$setValidity('phone', true);
                            } else {
                                ngModelController.$setValidity('phone', false);
                            }
                            return value;
                        }
                        var formatters=function(value){
                            if(value!==null&&value!==undefined){
                                //timeout
                                $timeout(()=>{
                                    parsers(value.toString());
                                },200);
                            }
                            return value;
                        }
                        ngModelController.$parsers.push(parsers);
                        ngModelController.$formatters.push(formatters);
                    },
                };
            });
