define(['angular'], function(angular) {
  return function(app, navsListA) {
    if(window.location.href.indexOf('isLookModuleInfo=1') >= 0) {
      app.config([
        'allRouterData',
        function(
          routerData,
        ) {
          var promiseKeyObj = {}, promiseObjByState={};
          var setPrintRepeatPromiseKey = function(item) {
            var resourceurl = item.resourceurl.split(';') || [];
            var promiseValue = resourceurl[0];
            if(promiseKeyObj[promiseValue]) {
              console.log('permissionKey:',item.resourcename,'==', promiseKeyObj[promiseValue].resourcename, item.resourceurl,'==', promiseKeyObj[promiseValue].resourceurl);
            }
            if(promiseValue) {
              promiseKeyObj[promiseValue] = item;
              var state = resourceurl[2] ? resourceurl[2].replace(/\/$/, '') : resourceurl[3];
              if(state) {
                promiseObjByState[state] = angular.extend({}, item, {
                  permissionKey: promiseValue
                });
              }
            }
          }

          angular.forEach(navsListA, function(item) {
            if(item.resourceurl && item.resourceurl.match(/^.*;.*;.*$/)) {
              setPrintRepeatPromiseKey(item);
            }
          });

          angular.forEach(routerData, function(item) {
            var state = '#/'+item.state.replace('.','/');
            if(promiseObjByState[state] && promiseObjByState[state].permissionKey !== item.permissionKey) {
              console.log('permissionKey:',promiseObjByState[state],item);
            }
            else if(!promiseObjByState[state] && item.inNav){
              state = '#/'+item.inNav.replace('.','/');
              if(promiseObjByState[state] && promiseObjByState[state].permissionKey !== item.permissionKey) {
                console.log('permissionKey:',promiseObjByState[state],item);
              }
            }
            else if(!promiseObjByState[state]){
              console.log(':',item);
            }
          })
        }])
    }

   

    navsListA = navsListA || [];
    angular.forEach(navsListA, function(item) {
      if(item.resourceurl && item.resourceurl.match(/^;.*;$/)) {
        item.icon = item.resourceurl.split(';')[1];
        item.resourceurl = ';';
      }
      else if(item.resourceurl && item.resourceurl.match(/^.*;.*;.*$/)) {
        item.icon = item.resourceurl.split(';')[1];
      }
    });

    var isRun = false;
    app.factory('navsData', [
      '$rootScope',
      '$location',
      '$state',
      'G',
      '$cookies',
      'allRouterData',
      function($rootScope, $location, $state, G, $cookies, allRouterData) {
        if (isRun) {
          return;
        }
        isRun = true;
        var permission = {}; //
        var allRouterObj = {}; //
        var returnData = {}; //
        var navsList = [];
        returnData.currentNavs = {};
        returnData.userOwnNavList = navsListA;

        //{state:obj}
        angular.forEach(allRouterData, function(routerItem) {
          allRouterObj[routerItem.state] = routerItem;
        });

        //
        var checkIfDupe = function(validNavsList, nav) {
          var isDupe = false;
          angular.forEach(validNavsList, function(validNav) {
            if (validNav.ehdrbacresourceid === nav.ehdrbacresourceid) {
              isDupe = true;
            }
          });
          return isDupe;
        };
        var getValidNavs = function() {
          var validNavsList = [],
            url = '';
          angular.forEach(navsListA, function(nav) {
            if (nav.resourcetype) {
              validNavsList.push(nav);
            } else {
              url = nav.resourceurl.split(';')[2];
              if (nav.resourceurl === ';' || (url && url[0] !== '#')) {
                validNavsList.push(nav);
              } else {
                if (url && url.indexOf('?') > -1) {
                  url = url.substring(0, url.indexOf('?'));
                }
                angular.forEach(allRouterData, function(routerItem) {
                  if (
                    url === routerItem.href &&
                    !checkIfDupe(validNavsList, nav)
                  ) {
                    validNavsList.push(nav);
                  }
                });
              }
            }
          });
          navsListA = validNavsList;
        };
        getValidNavs();

        // length
        var isPageUrl = false;
        var getPageUrl = function(item, selfId, parentId) {
          if (
            !item.resourceurl ||
            (item.resourceurl && item.resourceurl === ';')
          ) {
            angular.forEach(navsListA, function(currItem) {
              if (currItem && currItem[parentId] === item[selfId]) {
                if (
                  !currItem.resourceurl ||
                  (currItem.resourceurl && currItem.resourceurl === ';')
                ) {
                  getPageUrl(currItem, selfId, parentId);
                } else {
                  isPageUrl = true;
                }
              }
            });
          } else {
            isPageUrl = true;
          }
          return isPageUrl;
        };
        var getIsParentPageUrl = function(item, selfId, parentId) {
          var isParentPageUrl = false;
          angular.forEach(navsListA, function(currItem) {
            if (
              currItem &&
              currItem[selfId] === item[parentId] &&
              currItem.resourceurl !== ';' &&
              currItem.resourcetype === 0
            ) {
              isParentPageUrl = true;
            }
          });
          return isParentPageUrl;
        };
        var buttonsPermission = {};
        angular.forEach(navsListA, function(item) {
          isPageUrl = false;
          if (item.resourcetype !== 0) {
            //navsList.push(item);
            buttonsPermission[item.parentresourceid] =
              buttonsPermission[item.parentresourceid] || {};
            buttonsPermission[item.parentresourceid][item.resourceurl] = 1;
          } else if (
            getPageUrl(item, 'ehdrbacresourceid', 'parentresourceid') &&
            item.resourcetype === 0 &&
            item.resourceurl.indexOf(';') >= 0
          ) {
            // if(!getIsParentPageUrl(item, 'ehdrbacresourceid', 'parentresourceid')) {
            navsList.push(item);
            // }

            if (item.resourceurl && item.resourceurl !== ';') {
              var href = item.resourceurl.split(';')[2];
              href = href.replace('#/', '').replace('/', '.');
              if (href.indexOf('?') > -1) {
                href = href.substring(0, href.indexOf('?'));
              }
              if (allRouterObj[href]) {
                permission[allRouterObj[href].permissionKey] =
                  item.ehdrbacresourceid;
              }
            }
          }
        });
        var key;
        for (key in permission) {
          if (buttonsPermission[permission[key]]) {
            permission[key] = buttonsPermission[permission[key]];
          } else {
            permission[key] = 1;
          }
        }

        /*isChecked*/
        //
        var getCurrPermission = function(permission, $state) {
          var currPermission;
          var name =
            $state.current && $state.current.name
              ? $state.current.name
              : $state.name;
          if (name) {
            var currRouter = allRouterObj[name];
            if (currRouter.permissionKey) {
              currPermission = permission[currRouter.permissionKey];
            } else {
              currPermission = 1;
            }
          }
          return currPermission;
        };

        var getCurrentNavsByAddress = function(name) {
          var currHref = '#/' + name.replace(/\./g, '/');
          var isHasItem = getIsHasNav(returnData.data, currHref);
          angular.forEach(returnData.data, function(ones) {
            ones.isChecked = false;
            angular.forEach(ones.children, function(twos) {
              twos.isChecked = false;
              angular.forEach(twos.children, function(threes) {
                threes.isChecked = false;
                getIsChecked(threes, currHref, [ones, twos], isHasItem);
                if (threes.children) {
                  angular.forEach(threes.children, function(fours) {
                    fours.isChecked = false;
                    getIsChecked(
                      fours,
                      currHref,
                      [ones, twos, threes],
                      isHasItem
                    );
                    if (fours.children) {
                      angular.forEach(fours.children, function(fives) {
                        fives.isChecked = false;
                        getIsChecked(
                          fives,
                          currHref,
                          [ones, twos, threes, fours],
                          isHasItem
                        );
                      });
                    }
                  });
                }
              });
            });
          });
          returnData.getCurrentNavs();
        };

        var setResourceId = function(stateName) {
          var resourceId;
          returnData.userOwnNavList.forEach(function(item) {
            if (
              item.href &&
              item.href.slice(2).replace('/', '.') === stateName &&
              item.level !== 1
            ) {
              resourceId = item.ehdrbacresourceid;
            }
          });

          if (!resourceId) {
            allRouterData.forEach(function(d) {
              if (d.href && d.href.slice(2).replace('/', '.') === stateName) {
                returnData.userOwnNavList.forEach(function(t) {
                  if (
                    t.href &&
                    t.href.slice(2).replace('/', '.') === d.inNav &&
                    t.level !== 1
                  ) {
                    resourceId = t.ehdrbacresourceid;
                  }
                });
              }
            });
          }

          window._G.operatcontextid = G.operatcontextid = resourceId;
          $.ajax({
            type: 'POST',
            url: '/ehuodiBedrockApi/baseconfigcs/addOperateNotes',
            data: { operatcontextid: resourceId },
          });
        };

        //
        $rootScope.$on('$stateChangeStart', function(event, next, curr) {
          //
          angular.element('.modal-backdrop').remove();
          angular.element('body').removeClass('modal-open');
          getCurrentNavsByAddress(next.name);
          setResourceId(next.name);
          getPermission(next);
        });

        //tree
        var toTree = function toTree(dataList, pId, parent, self, needArray) {
          function minTree(dataList, pId, parent, self, tree) {
            tree.children = [];
            for (var i = 0, j = dataList.length; i < j; i++) {
              var item = dataList[i];
              if (item[parent] === pId) {
                tree.children.push(item);
                item.level = tree.level + 1;
                item.name = item.resourcename;
                minTree(dataList, item[self], parent, self, item);
              }
              var others =
                item.resourceurl.indexOf(';') >= 0
                  ? item.resourceurl.split(';')
                  : [item.resourceurl];

              item.href = others[2] || 'javascript:void(0);';
              item.icon = item.icon || others[1] || '';
              item.permissionKey = others[0];
              item._blank =
                others[2] && others[2].match(/^#\//) ? '' : '_blank';
            }
            if (tree.children && tree.children.length < 1) {
              tree.children = undefined;
            }
            return tree;
          }
          var tree = {
            level: 0,
          };
          tree = minTree(dataList, pId, parent, self, tree);
          if (!dataList || (dataList && !dataList.length)) {
            return [];
          }

          if (needArray) {
            return tree.children;
          }

          return tree.children && tree.children.length > 1
            ? tree.children
            : tree.children[0];
        };

        returnData.data = [];
        returnData.navsList = navsList;
        returnData.data = toTree(
          navsList,
          0,
          'parentresourceid',
          'ehdrbacresourceid',
          true
        );

        //
        var parseData = function(data) {
          angular.forEach(data, function(item) {
            if (
              item.children &&
              item.children[0].children &&
              item.children[0].children[0].children
            ) {
              item.href = item.children[0].children[0].children[0].href;
            } else if (item.children && item.children[0].children) {
              item.href = item.children[0].children[0].href;
            }
          });
          return data;
        };
        returnData.data = parseData(returnData.data);

        //

        returnData.getCurrentNavs = function() {
          var _this = this;
          angular.forEach(this.data, function(item) {
            if (item.isChecked) {
              _this.currentNavs.name = item.name;
              _this.currentNavs.href = item.href;
              _this.currentNavs.children = item.children;
            }
          });
        };

        //
        var parentHref = {};
        var getIsChecked = function(item, currHref, parents, isHasItem) {
          var itemState = item.href.replace('#/', '').replace('/', '.');
          var currState = currHref.replace('#/', '').replace('/', '.');
          if (item.href.indexOf('?') > -1) {
            item.isChecked =
              currHref === item.href.substring(0, item.href.indexOf('?'));
          } else {
            item.isChecked = currHref === item.href;
          }
          if (!item.isChecked) {
            var currRouter = allRouterObj[currState];
            if (currRouter.inNav === itemState && !isHasItem) {
              item.isChecked = true;
            }
          }
          if (item.isChecked) {
            angular.forEach(parents, function(parentItem) {
              parentItem.isChecked = true;
            });
          }

          if (item.children && item.children.length) {
            return;
          }
          if (!parentHref[parents[0].name]) {
            parents[0].href = item.href || parents[0].href;
          }
          parentHref[parents[0].name] = 1;
        };
        var getIsHasNav = function(children, currHref) {
          return children.some(item => {
            if (Array.isArray(item.children)) {
              return getIsHasNav(item.children, currHref);
            } else {
              return currHref === item.href;
            }
          });
        };

        //

        var parseInNavMenu = function(item) {
          var href = '';
          angular.forEach(item.children, function(child, index) {
            if (child.href) {
              href = child.href.replace('#/', '').replace('/', '.');
            }
            if (allRouterObj[href] && allRouterObj[href].inNav) {
              child.inNav = true;
            }
            if (child.children) {
              parseInNavMenu(child);
            }
          });
        };
        var areChildrenAllBtn = function(children) {
          var allBtn = false,
            i = 0,
            routerObj = {};
          angular.forEach(children, function(item) {
            if (item.resourcetype === 1 || item.inNav) {
              allBtn = true;
            }
          });
          return allBtn;
        };

        //
        var href = window.location.href;

        returnData.getIndex = function() {
          const data = returnData.data;
          if (!data.length) {
            return;
          }
          if (!$state.current.name && href.indexOf('#/') < 0) {
            var indexUrl = '';
            data[0].isChecked = true;
            if (
              data[0].children &&
              data[0].children[0] &&
              data[0].children[0].children
            ) {
              data[0].children[0].isChecked = true;
              data[0].children[0].children[0].isChecked = true;

              if (data[0].children[0].children[0].children) {
                data[0].children[0].children[0].children[0].isChecked = true;
                indexUrl =
                  data[0].children[0].children[0].children[0].href;
              } else {
                indexUrl = data[0].children[0].children[0].href;
              }
              if(indexUrl.indexOf('http://') >= 0 || indexUrl.indexOf('https://') >= 0) {
                window.location.href = indexUrl;
              }
              else {
                $location.path(indexUrl.replace('#', ''));
              }
              
              returnData.getCurrentNavs();
            }
          } else {
            var name =
              $state.current.name ||
              href
                .split('#/')[1]
                .replace('/', '.')
                .replace('/', '');

            var isHasRouter = false;
            if (allRouterObj[name]) {
              isHasRouter = true;
              setResourceId($state.current.name);
            }
            if (isHasRouter) {
              getCurrentNavsByAddress($state.current.name);
            } else {
              if (process.env.NODE_ENV === 'development') {
                window.location.href = '/404.html';
              } else {
                window.location.href = '/scms/404.html';
              }
            }
          }
        };

        function getPermission(state) {
          returnData.permission = getCurrPermission(permission, state);
          if (!returnData.permission) {
            G.alertNoPermission();
          }
        }

        const dataPromise = new Promise(resolve => {
          //
          getPermission($state);
          // setTimeout(() => {
            // getPermission($state);
            returnData.getIndex();
            resolve(returnData);
          // }, 0);
        });

        returnData.promise = dataPromise;

        return returnData;
        // return returnPromise;
      },
    ]);
  };
});
