;(function($) {
    
    var contentOverflow = function (ele, options) {
        this.$parent = ele;
        this.$element = ele.children('.latticeBox');
        this.defaults = {
            'showNum': 5,
            'childWidth':false
        };
        this.options = $.extend({}, this.defaults, options);
        this.init();
    }
    contentOverflow.prototype = {
        init: function(){
            var _this = this;
            this.left = 0;

            this.child = this.$element.children();
            this.childNum = this.$element.find('.lattice').length;
            this.lastBtn = this.$parent.find('.lastBtn');
            this.nextBtn = this.$parent.find('.nextBtn');
            this.ul = this.$element.find('ul');

            if(this.options.childWidth){
                var ml = parseInt(this.$element.find('.lattice').eq(0).css('margin-left'));
                var mr = parseInt(this.$element.find('.lattice').eq(0).css('margin-right'));
                var pl = parseInt(this.$element.find('.lattice').eq(0).css('padding-left'));
                var pr = parseInt(this.$element.find('.lattice').eq(0).css('padding-right'));
                var bl = parseInt(this.$element.find('.lattice').eq(0).css('border-left-width'));
                var br = parseInt(this.$element.find('.lattice').eq(0).css('border-right-width'));
                var w = parseInt((this.$element.width() - (ml+mr+pl+pr+bl+br)*this.options.showNum ) / this.options.showNum);
                this.$element.find('.lattice').css({width: w + 'px'});
                this.childWidth = this.$element.find('.lattice').eq(0).outerWidth(true);
            }else{
                this.childWidth = this.$element.find('.lattice').eq(0).outerWidth(true);
                this.$element.css({
                    'width': this.options.showNum * this.childWidth + 'px',
                    'position': 'relative',
                    'overflow': 'hidden'
                });
            }

        
            this.ul.css({
                'width': this.childNum * this.childWidth + 'px',
                'white-space': 'nowrap',
                'position': 'absolute',
                'left': this.left
            });

            if(this.childNum > this.options.showNum){
                this.$element.next().show();
                this.lastBtn.on('click',{_this:_this},this.last).hide();
                this.nextBtn.on('click',{_this:_this},this.next);
            }else{
                this.$element.next().hide();
            }
        },
        last: function(event){
            var _this = event.data._this;
            _this.left = parseInt($(_this.ul).position().left);
            if(_this.left >= 0){
                _this.left = 0;
            }else{
                _this.left += _this.childWidth;
                $(_this.nextBtn).show();
            }
            $(_this.ul).css({
                'left': parseInt(_this.left)
            });
            if(_this.left >= 0){
                $(this).hide(); 
            }
            return false;
        },
        next: function(event){
            var _this = event.data._this;
            _this.left = parseInt($(_this.ul).position().left);
            if(-_this.left == (_this.childNum - _this.options.showNum)*_this.childWidth){
                _this.left = -(_this.childNum - _this.options.showNum)*_this.childWidth;
            }else{
                if(_this.left % _this.childWidth){
                    _this.left = -(_this.childWidth-((_this.childWidth - _this.left) + _this.left));
                }
                _this.left -= _this.childWidth;
                $(_this.lastBtn).show();
            }
            $(_this.ul).css({
                'left': parseInt(_this.left)
            });
            if(-_this.left == (_this.childNum - _this.options.showNum)*_this.childWidth){
                $(this).hide(); 
            }
            return false;
        }
    }

    $.fn.contentOverflow = function(options) {
        this.each(function() {
            new contentOverflow($(this), options);
        });
        
    };

})(jQuery);