export default (app, elem, attrs, scope) => {
    app.controller('pageInclude', [
      '$scope',
      '$state',
      '$element',
      async function($scope, $state, $element) {
        $scope.pageUrl = decodeURIComponent($state.params.url);
        $element.find('iframe')[0].src = $scope.pageUrl;
      },
    ]);
  };
  