define([
    'angular'
], function(
) {
    return function(app, elem, attrs, scope) {
        app.controller('crumbCtrl', ['$scope', 'navsData', '$rootScope', '$stateParams', '$state','allRouterData', async function($scope, navsDataPromise, $rootScope, $stateParams, $state, allRouterData) {
                var currCrumbs = [];
                var data = {};
                var params,paramString;
                var key;
                $scope.crumbs = [];

                let navsData = await navsDataPromise;

                var getCrumbs = function(data) {
                    var href = '',
                        nextParams = $scope.nextParams || $state.params;
                    angular.forEach(data, function(item) {
                        if(item.isChecked) {
                            if(item.href.indexOf('#/pageInclude/index?') >= 0) {
                                if(item.href.indexOf(encodeURIComponent(nextParams.url)) >= 0) {
                                    $scope.crumbs = [item];
                                }                                
                            }
                            else {
                                if(item.level > 1 && item.href !== 'javascript:void(0);') {
                                    if(item.href&&item.href.indexOf('?')>-1){
                                        $state.current;
                                        href = item.href.substring(item.href.indexOf('?')+6)
                                        if(nextParams&&nextParams.href===decodeURIComponent(href)){
                                            $scope.crumbs.push(item);
                                        }
                                    }else{
                                        $scope.crumbs.push(item);
                                    }
                                }
                                getCrumbs(item.children);
                            }
                        }
                    });
                };

                var getRouteInfo = function(name) {
                    var currItem;
                    angular.forEach(allRouterData, function(item) {
                        if(item.state === name) {
                            currItem = item;
                            return item;
                        }
                    });
                    return currItem;
                };
                var getAllCrumbs = function(name, template) {
                    getCrumbs(navsData.data);
                    var currHref = '#/'+name.replace('.', '/'),
                        crumbsHref = $scope.crumbs[$scope.crumbs.length - 1]&&$scope.crumbs[$scope.crumbs.length - 1].href;
                    if(!$scope.crumbs || ($scope.crumbs && !$scope.crumbs.length)) {
                        return;
                    }
                    if(crumbsHref&&crumbsHref.indexOf('?')>-1){
                        crumbsHref = crumbsHref.substring(0,crumbsHref.indexOf('?'));
                    }
                    var currRouter = getRouteInfo(name);
                    if(currRouter.parentNav) {
                        var parentRouter = getRouteInfo(currRouter.parentNav);
                        var parentParams = parentRouter.params;
                        var href = '#/'+parentRouter.state.replace('.','/');
                        var params = [];
                        parentParams = parentParams.indexOf(',') > 0 ? parentParams.split(',') : [parentParams];
                        angular.forEach(parentParams, function(item) {
                            if(item) {
                                params.push(item+'='+($stateParams[item] || ''));
                            }
                        });
                        href = href + '?' + params.join('&');
                        $scope.crumbs.push({
                            href: href,
                            name: parentRouter.name
                        })
                    }
                    if(crumbsHref !== currHref && template) {
                        $scope.crumbs.push({
                            name: template.match(/[^\x00-\xff]+/g)[0]
                        });
                    };
                };

                getAllCrumbs($state.current.name, $state.current.template);

				$rootScope.$on('$stateChangeStart', function(event, next, nextParams) {
                    $scope.crumbs = [];
                    $scope.nextParams = nextParams;
                    getAllCrumbs(next.name, next.template);
                });
        }]);
    }
});