export default (app, elem, attrs, scope) => {
  app.controller('mainNavCtrl', [
    '$scope',
    'navsData',
    '$rootScope',
    '$timeout',
    '$state',
    async function($scope, originNavsData, $rootScope, $timeout, $state) {
      const navsData = await originNavsData;
      
      //
      var setPageIncludeChecked = function(navs, url) {
        angular.forEach(navs, function(item) {
          if(item.children) {
            setPageIncludeChecked(item.children, url);
          }
          else {
            if(item.href.indexOf('#/pageInclude/index') >= 0) {
              if(item.href.indexOf(encodeURIComponent(url)) >= 0) {
                item.isChecked = true;
              }
              else {
                item.isChecked = false;
              }
            }
          }
        })
      }
      //
      $scope.navs = navsData.currentNavs.children;
      setPageIncludeChecked($scope.navs, $state.params.url);
      $rootScope.$on('$stateChangeStart', function(event, next, curr) {
        $scope.navs = navsData.currentNavs.children;
        setPageIncludeChecked($scope.navs, curr.url);
      });
    },
  ]);
};
