/**
 * load other module
 * @author ryan.bian
 */
import loadjs from 'loadjs';
import assign from 'lodash/assign';
import scmsLoader from './scmsModules/';
import featureSupports from './utils/featureDetect';
import preload from './utils/preload';

let urlSearch = location.search;
if (urlSearch.charAt(0) === '?') {
  urlSearch = urlSearch.replace(/^\?/, '');
}
urlSearch = decodeURIComponent(urlSearch);
const urlParamGroup = urlSearch.split('&');
const urlParamMap = {};
urlParamGroup.forEach(group => {
  const [key, value] = group.split('=');
  let assignValue = value;
  if (key === 'debug') {
    assignValue = assignValue === 'true';
  } else if (key === 'moduleMap') {
    try {
      assignValue = JSON.parse(value);
    } catch (e) {
      assignValue = undefined;
    }
  }
  assign(urlParamMap, {
    [key]: assignValue,
  });
});

const PUBLIC_PATH = location.origin ? location.origin : `${location.protocol}//${location.hostname}` + (location.port ? `:${location.port}` : '');
const REGISTED_MODULE_LIST = new Map([
  ['crmModules', `${PUBLIC_PATH}/crm`],
  ['orderModules', `${PUBLIC_PATH}/orderMotionWeb`],
  ['wareHouseModules', `${PUBLIC_PATH}/wareHouse`],
  ['kaModules', `${PUBLIC_PATH}/kaAdmin`],
  ['kaWoodpeckerModules', `${PUBLIC_PATH}/kaWoodpeckerWeb`],
  ['marketWebModules', `${PUBLIC_PATH}/marketWeb`],
  ['autoInsuranceModules', `${PUBLIC_PATH}/carInsurance`],
  ['qosModules', `${PUBLIC_PATH}/qos`],
  ['reportWebModules', `${PUBLIC_PATH}/reportWeb`],
  ['woodpeckerModules', `${PUBLIC_PATH}/woodpeckerWeb`],
  ['wisdomConnectModules', `${PUBLIC_PATH}/wisdomConnectWeb`],
  ['basicConfigModules', `${PUBLIC_PATH}/basicConfigWeb`],
  ['logSystemModules', `${PUBLIC_PATH}/logSystemWeb`],
  ['appPublishModules', `${PUBLIC_PATH}/appPublishWeb`],
]);

const MODULE_ENTIIES = {};
window.MODULE_LOADER = {
  scmsModules: scmsLoader,
};
const PROTOCAL = 'http';

const setupLoadingList = async () => {
  const list = [];
  const t = new Date().getTime();
  for (const [name, url] of REGISTED_MODULE_LIST.entries()) {
    try {
      // const res = await fetch(`${url}/manifest.json` + (supportPreload ? '' : `?t=${t}`), { mode: 'cors' });
      const res = await fetch(`${url}/manifest.json?t=${t}`, { mode: 'cors' });
      const manifestData = await res.json();
      const preloadList = [];
      if (featureSupports.preload) {
        Object.keys(manifestData).forEach(assetsName => {
          if (assetsName !== 'app.js' && assetsName !== 'app.css') {
            const extName = assetsName.split('.').pop();
            if (extName === 'css' || extName === 'js') {
              preloadList.push(
                manifestData[assetsName].startsWith(PROTOCAL) ?
                  manifestData[assetsName] : `${url}/${manifestData[assetsName]}`
              );
            }
          }
        });
      }
      list.push(new Promise((resolve, reject) => {
        const entryList = [
          manifestData['app.js'].startsWith(PROTOCAL) ?
            manifestData['app.js'] : `${url}/${manifestData['app.js']}`, 
          ];
        if (manifestData['app.css']) {
          entryList.push(
            manifestData['app.css'].startsWith(PROTOCAL) ?
              manifestData['app.css'] : `${url}/${manifestData['app.css']}`
          );
        }
        loadjs(entryList, {
          success: () => {
            if (window.MODULE_REGISTER[name]) {
              assign(MODULE_ENTIIES, {
                [name]: window.MODULE_REGISTER[name],
              });
              window.MODULE_LOADER[name] = window.MODULE_REGISTER[name].loader;
            }
            preloadList.forEach(url => {
              preload(url);
            });
            resolve(name);
          },
          error: pathsNotFound => {
            console.warn('pathsNotFound', pathsNotFound);
            resolve(name);
          },
        });
      }));
    } catch (e) {
      console.warn(e);
      console.warn('error context', {
        name,
        url,
      });
    }
  }
  return list;
};

const assignModules = async () => {

  if (urlParamMap.debug) {
    const defaultModuleMap = {};
    for (const [name, url] of REGISTED_MODULE_LIST.entries()) {
      Object.assign(defaultModuleMap, {
        [name]: url,
      });
    }
    const { createDevTool } = await import('./devTool');
    const devTool = createDevTool(defaultModuleMap);
    const devStore = devTool.store;
    let moduleMap;
    if (urlParamMap.moduleMap) {
      moduleMap = urlParamMap.moduleMap;
    } else {
      moduleMap = await devStore.read();
    }
    Object.keys(moduleMap)
      .forEach(name => {
        REGISTED_MODULE_LIST.set(name, moduleMap[name]);
      });
    console.log(REGISTED_MODULE_LIST);
  }

  const loadingList = await setupLoadingList();
  try {
    await Promise.all(loadingList);
  } catch (e) {
    console.warn(e);
  }
  return MODULE_ENTIIES;
};

export default assignModules;