import ctrl from './mainNavCtrl';
import scmsmodules from 'scmsmodules';
import html from './mainNav.html';
import '../../configs/navsData';

const mainNavDirective = scmsmodules['mainNav/mainNavDirective'];

export default (app, elem, attrs, scope) => {
  ctrl(app, elem, attrs, scope);
  mainNavDirective(app, elem, attrs, scope);
  elem.append(html);
}