//App
angular.module('components', []).config([
  '$compileProvider',
  '$controllerProvider',
  '$provide',
  '$filterProvider',
  function($compileProvider, $controllerProvider, $provide, $filterProvider) {
    var cache = {};
    var app = {};
    var ngMap = {
      controller: $controllerProvider,
      directive: $compileProvider,
      provider: $provide,
      factory: $provide,
      service: $provide,
      constant: $provide,
      value: $provide,
      decorator: $provide,
      filter: $filterProvider,
    };

    for (var key in ngMap) {
      app[key] = (function(key) {
        return function(name, fn) {
          return wapper(key, name, arguments);
        };
      })(key);
    }

    function wapper(key, name, args) {
      var hasKey =
        'has' +
        key.replace(/^[a-z]/, function(a) {
          return a.toUpperCase();
        });

      if (!cache[hasKey]) {
        cache[hasKey] = {};
      }

      if (!cache[hasKey][name]) {
        cache[hasKey][name] = true;
        if (key === 'controller' || key === 'filter') {
          return ngMap[key]['register'].apply(ngMap[key], args);
        }

        return ngMap[key][key].apply(ngMap[key], args);
      }
    }

    $compileProvider.directive('ngComponent', [
      '$compile',
      function($compile) {
        // console.log('my----ngController')
        return {
          scope: true,
          priority: 500,
          link: function(scope, elem, attrs) {
            var componentUrl = attrs.ngComponent;
            if ('ngLoading' in attrs) {
              elem.html(
                '<div class="sk-spinner sk-spinner-wave"> <div class="sk-rect1"></div> <div class="sk-rect2"></div> <div class="sk-rect3"></div> <div class="sk-rect4"></div> <div class="sk-rect5"></div> </div> </div>'
              );
						}
						// TODO:  componentUrl  scmsModules
            const temp = componentUrl.split('/');
            const componentModule = temp.shift();
            const componentPath = temp.join('/');
            if (window.MODULE_LOADER[componentModule]) {
              const componentFn = window.MODULE_LOADER[componentModule](componentPath);
              if (componentFn) {
                componentExec(componentFn, componentModule);
              } else {
                console.warn('');
                console.warn('error context', {
                  componentUrl,
                  componentModule,
                  componentPath,
                });
              }
            } else {
              console.warn(``);
              console.warn('error context', {
                componentModule,
                componentPath,
              });
            }

            async function componentExec(componentFn, componentModule) {
              let $currScope = scope.$parent;
              const exeComponentFn = () => componentFn;
              try {
                let fn = await exeComponentFn();
                fn = fn.default ? fn.default : fn;
                if (!angular.isFunction(fn)) {
                  console.warn(
                    '--:' + componentUrl
                  );
                }
                elem.html('');
                await fn(app, elem, attrs, $currScope);
                $compile(elem.contents())($currScope);
                if (!scope.$$phase) {
                  scope.$apply();
                }
                var componentName = getComponentName(componentUrl);
                window._G.myScope = window._G.myScope || {};
                window._G.myScope[componentName] = $currScope.$$childTail;
                $currScope.$$childTail.$$componentName = componentName;
              } catch (e) {
                if (e.toString().includes('Error: Loading chunk')) {
                  console.log('module loaded failed', componentModule, e.toString())
                  window._G.alert('<p style="padding: 10px 0;"><br>"<span style="color:#009663;"></span>"</p>', {
                    type: 'confirm',
                    title: '',
                    callback() {
                      window.location.reload();
                    },
                  });
                } else {
                  console.warn(e);
                }
              }
            }

            function getComponentName(componentUrl) {
              var last = componentUrl.lastIndexOf('/');
              var componentName = componentUrl.substr(last + 1);
              return componentName;
            }
          },
        };
      },
    ]);
  },
]);
