define(['angular'], function(angular) {
  return function(app) {
    app
      .config([
        '$compileProvider',
        '$controllerProvider',
        '$provide',
        '$httpProvider',
        function(
          $compileProvider,
          $controllerProvider,
          $provide,
          $httpProvider
        ) {
          $httpProvider.interceptors.push('apiIntercepter');
        },
      ])
      .factory('apiIntercepter', [
        '$q',
        'G',
        '$cookies',
        function($q, G, $cookies) {
          var concurrentAjaxLock = {}; //

          //URL
          function lockConcurrentAjax(config) {
            if (config && config.noconcurrent) {
              //
              var url = config.url;
              if (concurrentAjaxLock[url] != null) {
                return true;
              }
              concurrentAjaxLock[url] = url;
            }

            return false;
          }
          //URL
          function unLockConcurrentAjax(config) {
            if (config && config.noconcurrent) {
              //
              var url = config.url;
              //500URL
              window.setTimeout(function() {
                concurrentAjaxLock[url] = null;
              }, 500);
            }
          }

          var apiIntercepter = {
            request: function(config) {
              var url = config.url;

              if (lockConcurrentAjax(config)) {
                var deferred = $q.defer();
                return deferred.promise;
              }

              if (config.isDebug && (G.envType === 'TEST'|| G.envType === 'DEVELOPMENT')) {
                if (config.test) {
                  config.method = 'get';
                  url = config.test + '?v=' + new Date().getTime();
                } else {
                  url = 'http://apigo.tf56.lo/ehuodiApigo' + url;
                }
              }

              if (
                angular.isString(config.method) &&
                config.method.toLocaleLowerCase() != 'post' &&
                angular.isObject(config.data)
              ) {
                url += url.indexOf('?') == -1 ? '?' : '&';
                url += $.param(config.data);
              }

              config.responseType = 'json';
              config.withCredentials = true;
              config.url = url;
              config.data = angular.extend({}, config.data) || {};
              for(let param in config.data){
                if(config.data[param]&&(typeof config.data[param].trim)==='function'){
                  config.data[param] = config.data[param].trim();
                }
              }
              
              if (!config.data.disableDateHandler) {
                if (config.data.datestart) {
                  if (config.data.datestart.length > 10) {
                    config.data.datestart = config.data.datestart;
                  } else {
                    config.data.datestart = config.data.datestart + ' 00:00:00';
                  }
                } else {
                  delete config.data.datestart;
                }
                if (config.data.dateend) {
                  if (config.data.dateend.length > 10) {
                    config.data.dateend = config.data.dateend;
                  } else {
                    config.data.dateend = config.data.dateend + ' 23:59:59';
                  }
                } else {
                  delete config.data.dateend;
                }
                if (config.data.startdate) {
                  if (config.data.startdate.length > 10) {
                    config.data.startdate = config.data.startdate;
                  } else {
                    config.data.startdate = config.data.startdate + ' 00:00:00';
                  }
                } else {
                  delete config.data.startdate;
                }
                if (config.data.enddate) {
                  if (config.data.enddate.length > 10) {
                    config.data.enddate = config.data.enddate;
                  } else {
                    config.data.enddate = config.data.enddate + ' 23:59:59';
                  }
                } else {
                  delete config.data.enddate;
                }
                if (config.data.startDate) {
                  if (config.data.startDate.length > 10) {
                    config.data.startDate = config.data.startDate;
                  } else {
                    config.data.startDate = config.data.startDate + ' 00:00:00';
                  }
                } else {
                  delete config.data.startDate;
                }
                if (config.data.endDate) {
                  if (config.data.endDate.length > 10) {
                    config.data.endDate = config.data.endDate;
                  } else {
                    config.data.endDate = config.data.endDate + ' 23:59:59';
                  }
                } else {
                  delete config.data.endDate;
                }
                if (config.data.dateStart) {
                  if (config.data.dateStart.length > 10) {
                    config.data.dateStart = config.data.dateStart;
                  } else {
                    config.data.dateStart = config.data.dateStart + ' 00:00:00';
                  }
                } else {
                  delete config.data.dateStart;
                }
                if (config.data.dateEnd) {
                  if (config.data.dateEnd.length > 10) {
                    config.data.dateEnd = config.data.dateEnd;
                  } else {
                    config.data.dateEnd = config.data.dateEnd + ' 23:59:59';
                  }
                } else {
                  delete config.data.dateEnd;
                }
              } else {
                delete config.data.disableDateHandler;
              }
              config.data.operatcontextid = G.operatcontextid;
              if (
                angular.isString(config.method) &&
                config.method.toLocaleLowerCase() === 'post'
              ) {
                if (config.isJson) {
                  config.headers = { 'Content-Type': 'application/json' };
                } else {
                  config.data = $.param(config.data);
                  config.headers = {
                    'Content-Type': 'application/x-www-form-urlencoded',
                  };
                }
              }
              if (config.method === 'POST' && config.url.indexOf('.json') > 0) {
                config.method = 'get';
              }
              return config;
            },
            response: function(res) {
              unLockConcurrentAjax(res.config);
              var data = res.data;
              if (
                data &&
                data.result === 'error' &&
                data.msg === 'authorityFailure'
              ) {
                G.doLogin();
                return {};
              }
              return res;
            },
            requestError: function(rejection) {
              //hideLoadingBtn(rejection.config);
              // console.error('')
              //return $q.reject(rejection);
            },
            responseError: function(rejection) {
              rejection = rejection || {};

              if (rejection.config) {
                unLockConcurrentAjax(rejection.config);
              }

              if (!$cookies.jobcard) {
                G.doLogin();
                return;
              }
              if (!rejection.config) {
                return;
              }
              G.console(
                'error: apiUrl=' +
                  rejection.config.url +
                  '; ' +
                  (rejection.statusText || '')
              );
              return $q.reject(rejection);
            },
          };

          return apiIntercepter;
        },
      ]);
  };
});
