define(['angular'], function() {
  return function(app, routerDataName) {
    app.config([
      '$stateProvider',
      '$urlRouterProvider',
      routerDataName,
      '$httpProvider',
      '$compileProvider',
      function(
        $stateProvider,
        $urlRouterProvider,
        routerData,
        $httpProvider,
        $compileProvider
      ) {
        $compileProvider.aHrefSanitizationWhitelist(
          /^\s*(https?|ftp|mailto|file|javascript):/
        );
        $httpProvider.defaults.useXDomain = true;
        delete $httpProvider.defaults.headers.common['X-Request-With'];
        const states = {};
        routerData.forEach(item => {
          // statestate
          item.href = '#/' + item.state.replace(/\./g, '/');
          let paths = item.state.split(/\./g);
          let defaultState = paths[paths.length - 1];
          let currState = [];
          if (paths.length > 1) {
            paths = paths.slice(0, paths.length - 1);
            paths.forEach(path => {
              currState.push(path);
              Object.assign(states, {
                [currState.join('.')]: {
                  abstract: true,
                  url: '/' + currState.join('/'),
                  template: '<div ui-view></div>',
                  paramsObj: item.paramsObj,
                }
              });
            });
          } else {
            currState.push(defaultState);
          }
          currState.push(defaultState);
          Object.assign(states, {
            [currState.join('.')]: {
              url: 
                '/' + currState[currState.length - 1] + (item.params ? '?' + item.params : ''),
              template: `<div text="${item.name}" ng-component="${item.moduleName}/${currState.join('/')}"></div>`,
              paramsObj: item.paramsObj,
            },
          });

          //
          if (item.isIndex) {
            $urlRouterProvider.when('', item.href.replace(/^#/, ''));
          }
        });
        Object.keys(states).forEach(key => {
          const item = states[key];
          $stateProvider.state(key, {
            url: item.url,
            template: item.template,
            params: item.paramsObj,
          });
        });
      },
    ]);
  };
});
