define(['angular'], function(angular) {
  return function(app, elem, attrs, scope) {
    app.controller('headerCtrl', [
      '$scope',
      '$cookies',
      '$location',
      '$state',
      'headerService',
      'G',
      '$timeout',
      '$window',
      'navsData',
      '$rootScope',
      async function(
        $scope,
        $cookies,
        $location,
        $state,
        service,
        G,
        $timeout,
        $window,
        originNavsData,
        $rootScope
      ) {
        let navsData = await originNavsData.promise;
        //
        if (!$cookies.jobcard) {
          G.doLogin();
        } else if (!navsData.data || (navsData.data && !navsData.data.length)) {
          G.alertNoPermission();
        }

        if (G.userInfo.versionUpgrade) {
          $('#versionUpgradeLog').modal({
            backdrop: 'static',
          });
        }

        $scope.getBackgrunt = function(areacode){
          service.selectFbcmslogo({
            areacode:areacode,
            clientid:'fbcmspc'
          }).then((data)=>{
            if(data.data && data.data.result === 'success' && data.data.data){
              $scope.backgruntType = true;
              $scope.backgruntImg = data.data.data.imageurl;
              $scope.backgruntColor = JSON.parse(data.data.data.extradata).backgroundcolor;
            }else{
              $scope.backgruntType = false;
              $scope.backgruntColor = '1f74e9';
            }
          });
        }
        
        $scope.userInfo = G.userInfo;

        $scope.userOrgGroup = [];
        try {
          var orgList = [];
          angular.forEach($scope.userInfo.userorgpros, function(item) {
            if (orgList.indexOf(item.organizationname) < 0) {
              orgList.push(item.organizationname);
              $scope.userOrgGroup.push({
                name: item.organizationname,
                value: item.organizationcode,
                type: item.organizationtype,
                professionList: [
                  {
                    professioncode: item.professioncode,
                    professionname: item.professionname,
                  },
                ],
                userprofession: item.professioncode,
              });
            } else {
              $scope.userOrgGroup[
                $scope.userOrgGroup.length - 1
              ].professionList.push({
                professioncode: item.professioncode,
                professionname: item.professionname,
              });
            }
          });
          $scope.professionList = $scope.userOrgGroup[0].professionList;
          $scope.getBackgrunt($scope.userOrgGroup[0].value);
        } catch (e) {}

        $scope.currentOrg = $scope.userInfo.organizationname;
        $scope.currentProfession = $scope.userInfo.professionname;

        $scope.image = '';

        //
        $scope.$on('$stateChangeSuccess', function(
          e,
          toState,
          toParams,
          fromState,
          fromParams
        ) {
          if (
            angular.element('.nav-second-level') &&
            angular.element('.nav-second-level').length
          ) {
            angular.element('.nav-second-level').removeAttr('style');
          }
        });

        $scope.navData = G.clone(navsData.data);

        //
        $scope.loginOut = function() {
          service.loginOut().then(
            function(data) {
              if (
                data &&
                data.data &&
                data.data &&
                data.data.result === 'success'
              ) {
                $cookies = {};
                $scope.userInfo = {};
                G.doLogin();
              }
            },
            function() {}
          );
        };

        $scope.showAllNavFn = function() {
          $scope.showAllNav = true;
        };

        function setCurrentOrg(org){
          $scope.currentOrg = org.name;
          $scope.professionList = org.professionList;
          $scope.currentProfession = $scope.professionList[0].professionname;
          G.userInfo.professionname = $scope.currentProfession;
          G.userInfo.professioncode = $scope.professionList[0].professioncode;
          G.userInfo.organizationtype = org.type;
          G.userInfo.organizationcode = org.value;
          G.userInfo.organizationname = org.name;
          G.userInfo.userprofession = org.userprofession;
          G.cityListOfHaveRight = [];
        }

        function setProfession(profession){
          $scope.currentProfession = profession.professionname;
          G.userInfo.professionname = profession.professionname;
          G.userInfo.professioncode = profession.professioncode;
          G.userInfo.userprofession = profession.professionname;
        }

        $scope.setCurrent = function(org) {
          setCurrentOrg(org)
        };

        $scope.changeProfession = function(profession, org) {
          if($scope.currentOrg===org.name){
            setProfession(profession);
          }else{
            setCurrentOrg(org);
            setProfession(profession);
          }
          $scope.getBackgrunt(org.value);
          $rootScope.$broadcast('updateOrg');
        };

        $scope.removeHeight = function(item) {
          $scope.setActive(item);
          if (
            angular.element('.nav-second-level') &&
            angular.element('.nav-second-level').length
          ) {
            angular.element('.nav-second-level').removeAttr('style');
          }
        };

        $scope.setActive = function(item) {
          angular.forEach($scope.navData, function(nav) {
            nav.isChecked = false;
          });
          angular.forEach($scope.hideMenu, function(nav) {
            nav.isChecked = false;
          });
          item.isChecked = true;
        };
        
        $scope.toggleNav = function(){
          $scope.showUserCenter = !$scope.showUserCenter;
          $timeout(function(){
            if($scope.showUserCenter){
              $('.header-right').focus();
            }
          },0);
        };

        $scope.closeNav = function(){
          $scope.showUserCenter = false;
          $('body').scrollTop(0);
        };

        $scope.toggleTodo = function(){
          $scope.showTodo = !$scope.showTodo;
          getInfoList(1);
          $timeout(function(){
            if($scope.showTodo){
              $('.header-right').focus();
            }
          },0);
        };

        $scope.closeTodo = function(){
            $scope.showTodo = false;
        };

        //
        $scope.gotoCenter = function() {
          window.location.href = G.urls.center[G.envType];
        };

        $scope.gotoPmTool = function(){
          window.open('/pmTool/');
        };

        $scope.innerJump = function(url, params){
          if(params){
            $state.go(url, params);
          }else{
            $state.go(url);
          }
          $scope.showUserCenter = false;
          $scope.closeTodo();
        };

        $scope.jumpPage = function(url){
          window.location = url;
          $scope.closeTodo();
        }

        $scope.hideMenu = [];
        function resizeMenu(container, menuWidth, index) {
          var menuIndex = index || $scope.navData.length - 1;
          if (menuIndex < 0) {
            return;
          }
          var lastMenuWidth = angular.element('.header-ctrl .nav-menu li')[menuIndex].offsetWidth;
          var angleWidth = angular
            .element('.header-ctrl .fa-angle-double-right')
            .width();
          if (
            (container - menuWidth > lastMenuWidth &&
              !$scope.hideMenu.length) ||
            (container - menuWidth > angleWidth + lastMenuWidth &&
              $scope.hideMenu.length)
          ) {
            if ($scope.hideMenu.length) {
              $scope.navData.push($scope.hideMenu.shift());
            }
            return;
          } else if (container-menuWidth <= angleWidth){
            menuWidth = menuWidth - lastMenuWidth;
            $scope.hideMenu.unshift($scope.navData.pop());
            var nextMenuWidth = angular.element('.header-ctrl .nav-menu li')[menuIndex-1].offsetWidth;
            if(container-menuWidth<angleWidth+nextMenuWidth){
              resizeMenu(container, menuWidth, index-1);
            }
          }
        }

        $scope.domReady = function(last) {
          if (last) {
            $timeout(function() {
              var bodyWidth = angular.element('body')[0].offsetWidth;
              var logoWidth = angular.element('.header-ctrl .logo')[0]
                .offsetWidth;
              var loginWidth = angular.element('.header-ctrl .login-status')[0]
                .offsetWidth;
              var menuWidth = angular.element('.header-ctrl .nav-menu')[0]
                .offsetWidth;
              var menuContainer = bodyWidth - logoWidth - loginWidth;

              resizeMenu(menuContainer, menuWidth);
            });
          }
        };

        var timer;
        $(window).on('resize', function() {
          timer && clearTimeout(timer);
          timer = $timeout(function() {
            var bodyWidth = angular.element('body')[0].offsetWidth;
            var logoWidth = angular.element('.header-ctrl .logo')[0]
              .offsetWidth;
            var loginWidth = angular.element('.header-ctrl .login-status')[0]
              .offsetWidth;
            var menuWidth = angular.element('.header-ctrl .nav-menu')[0]
              .offsetWidth;
            var menuContainer = bodyWidth - logoWidth - loginWidth;

            resizeMenu(menuContainer, menuWidth);
          });
        });

        function getInfoList(currPage){
            $scope.pageSize = 6;
            $scope.currentPage = currPage || $scope.currentPage || 1;
            service.selectWorkThingList({
                direction: 'RECEIVE',
                status: 'TODO',
                skipCount: ($scope.currentPage - 1) * $scope.pageSize,
                pageSize: $scope.pageSize
            }).then(function(data){
                if(data.data&&data.data.result==='success'){
                    $scope.infoData = data.data.data;
                    $scope.totalCount = data.data.count;
                    $scope.msgTotalCount = $scope.totalCount;
                  }
            });
        }
        getInfoList();

        $scope.markAsRead = function(item, e, url){
          if(e){
              e.stopPropagation();
          }
          service.dealMsg({
              workthingid: item.workthingid
          }).then(function(data){
              if(data.data&&data.data.result==='success'){
                  item.status = 'DONE';
                  $scope.msgTotalCount--;
                  if(url){
                    $scope.innerJump(url,{id:item.workthingid})
                  }
              }
          },function(){});
        };

        $scope.onchanged = function(){
          getInfoList();
        };

        $scope.handleMsg = function(item, e){
          let url = item.url.substring(item.url.indexOf('#'),item.url.length-1)
          let state = url.split('/');
          $state.go(`${state[1]}.${state[2]}`);
          $scope.closeTodo();
          $scope.markAsRead(item);
          e.stopPropagation();
        };

        function checkIfNeedTip(){
          var textEdit = angular.element('.text-ellipsis');
          var clientWidth = 0, scrollWidth = 0, index = 0;  
          angular.forEach(textEdit, function(item, index){
              clientWidth = item.clientWidth;
              scrollWidth = item.scrollWidth;
              // index = item.id;
              if(clientWidth<scrollWidth&&$scope.infoData.workthingvolist[index]){
                  $scope.infoData.workthingvolist[index].tipShow = true;
              }
          });
        }
        $scope.ready = function(last){
          if(last){
              $timeout(function(){
                  checkIfNeedTip();
              },1000);
          }
        };

        $rootScope.$on('$stateChangeStart', function() {
          //
          getInfoList(1)
        });

        $scope.$on('updateMsgCount', function() {
          //
          getInfoList(1)
        });

      },
    ]);
  };
});
