import ctrl from './ctrl';
import css from './index.less';

export default (app, elem, attrs, scope) => {
  ctrl(app, elem, attrs, scope);
  var html = '<div ng-controller="pageInclude" class="pageInclude box content-padding"><a class="btn btn-info" href="{{pageUrl}}" target="_blank"><i class="fa fa-angle-double-right"></i></a><div><iframe></iframe></div></div>';
  elem.append(html);
}