import endsWith from 'lodash/endsWith';
import assign from 'lodash/assign';

const doc = document;
const docHead = doc.head;

const DEFAULT_ATTRS = {
  rel: 'preload',
};

export default href => {
  const preloadLink = doc.createElement('link');
  const attrs = assign({}, DEFAULT_ATTRS, {
    href,
  });
  if (endsWith(href, '.js')) {
    assign(attrs, {
      as: 'script',
    });
  } else if (endsWith(href, '.css')) {
    assign(attrs, {
      as: 'style',
    });
  } else if (endsWith(href, '.jpg') || endsWith(href, '.png') || endsWith(href, '.gif')) {
    assign(attrs, {
      as: 'image',
    });
  }
  Object.keys(attrs).forEach(attr => {
    preloadLink.setAttribute(attr, attrs[attr]);
  });
  docHead.appendChild(preloadLink);
};