        formatDate: function (date, fmt) {
          date = date || new Date();
          var o = {
            "M+": date.getMonth() + 1, // 
            "D+": date.getDate(), // 
            "h+": date.getHours(), // 
            "m+": date.getMinutes(), // 
            "s+": date.getSeconds(), // 
            "q+": Math.floor((date.getMonth() + 3) / 3), // 
            "S": date.getMilliseconds()
            // 
          };
          if (/(Y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
          for (var k in o)
            if (new RegExp("(" + k + ")").test(fmt))
              fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
          return fmt;
        },

        buildAlertTemplate: function () {
          const html = `
            <div class="ui-alert-top">
              <span id="alertTypeIcon"></span>
              <p class="content"></p>
            </div>
          `;
          $("body").append(html);
        },

				/*
				 *item
				 *scope: isAllActive
				 *data: 
				*/
        checkedItem: function (item, scope, data) {
          var isAllActive = true;
          var isHasActive = false;
          if (item) {
            item.isActive = !item.isActive;
          }
          data.forEach(function (list) {
            if (!item && !scope.isAllActive) {
              list.isActive = true;
            }
            else if (!item && scope.isAllActive) {
              list.isActive = false;
            }
            if (!list.isActive) {
              isAllActive = false;
            }
            else {
              isHasActive = true;
            }
          });
          scope.isAllActive = isAllActive;
          scope.isHasActive = isHasActive;
          scope.checkedUpdate = (+new Date());
        },

        //
        loading: function (isShow, options = {}) {
          options.$container = options.$container || $('#container');
          $.loading(isShow, options);
        },

        /*
         * 
         * options.msg: string 
         * options.submit: fn 
         * options.close: fn 
        */
        confirm: function (options) {
          var $container = $('#container')
          var $alert = $container.find('.tx_popover');
          var scrollHeight = $container[0].scrollHeight;
          if (!$alert.length) {
            $alert = $('<div class="tx_popover popover fade in"><div class="arrow" style="top: 50%;"></div><div class="popover-content mt10 content"></div><div class="popover-content" style="text-align:center;"><button class="btn btn-success btn-sm" name="submit"></button><button name="calson" class="btn btn-default btn-sm ml10"></button></div></div>');
            $container.append($alert);
            var _this = this;
            $container.bind('mousedown', function (event) {
              if ($alert && $alert.css('display') === 'block' && !$(event.target).parents('.tx_popover').length) {
                $alert.hide();
              }
            });
          }
          if (options.msg) {
            $alert.find('.content').html(options.msg);
          }
          options.position = options.position || 'right';
          $alert.find('.arrow').css({
            top: options.position === 'right' ? '50%' : ''
          });
          var offset = options.$target.offset();
          var width = options.$target.width();
          var height = options.$target.height();
          //
          // if(scrollHeight - offset.top - height - $alert.outerHeight() - 10 < 0 ){
          // 	options.position = 'top';
          // }
          $alert.removeClass('top');
          $alert.removeClass('right');
          $alert.removeClass('left');
          $alert.removeClass('bottom');
          $alert.addClass(options.position);

          var top = 0, left = 0;
          offset.left -= $('#mainNav').width();
          offset.top -= $('#header').height();
          offset.top += $container.scrollTop();

          if (options.position === 'right') {
            $alert.css({
              'margin-top': - $alert.height() / 2 + 'px',
              'margin-left': '10px'
            });
            top = offset.top + height / 2 + 'px';
            left = offset.left + width + 'px';
          }
          if (options.position === 'top') {
            $alert.css({
              'margin-left': - $alert.width() / 2 - 15 + 'px',
              'margin-top': '-10px'
            });
            top = offset.top - $alert.outerHeight() + 'px';
            left = offset.left + width + 'px';
          }
          if (options.position === 'left') {
            $alert.css({
              'margin-top': - $alert.height() / 2 + 'px',
              'margin-left': '-10px'
            });
            top = offset.top + height / 2 + 'px';
            left = offset.left - $alert.outerWidth() + 'px';
          }
          if (options.position === 'bottom') {
            $alert.css({
              'margin-left': - $alert.width() / 2 - 15 + 'px',
              'margin-top': '10px'
            });
            top = offset.top + height + 'px';
            left = offset.left + width + 'px';
          }

          $alert.css({
            'max-width': '400px'
          });
          $alert.css({
            'min-width': '142px'
          });
          $alert.css({
            left: left,
            top: top
          });
          $alert.show();

          $alert.unbind();
          $alert.delegate('[name="submit"]', 'click', function () {
            if (options.submit) {
              options.submit();
            }
          });
          $alert.delegate('[name="calson"]', 'click', function () {
            $alert.hide();
            if (options.close) {
              options.close();
            }
          });
          this.$alert = $alert;
          return $alert;
        },

