        ajax: function (functionName, params) {
          var _this = this;
          if (params.showWait) {
            _this.showWait();
          }
          functionName(params.data).then(function (data) {
            if (data.data == null) {
              if (params.error) {
                params.error(data);
              }
              else {
                _this.alert(data.msg || "", { type: 'error' });
              }
              if (params.complete) {
                params.complete();
              }
              if (params.showWait) {
                _this.hideWait();
              }
            }
            else {
              data = data.data;
              if (data.result == 'success') {
                if (params.success) {
                  params.success({ data: data.data, count: data.count });
                }
                if (params.complete) {
                  params.complete();
                }
                if (params.showWait) {
                  _this.hideWait();
                }
              }
              else {
                if (params.error) {
                  params.error(data);
                }
                else {
                  _this.alert(data.msg || "", { type: 'error' });
                }
                if (params.complete) {
                  params.complete();
                }
                if (params.showWait) {
                  _this.hideWait();
                }
              }
            }
          },
            function (data) {
              if (params.error) {
                params.error(data);
              }
              else {
                _this.alert("", { type: 'error' });
              }
              if (params.showWait) {
                _this.hideWait();
              }
              if (params.complete) {
                params.complete();
              }
            });
        },

