import angular from 'angular';
import assign from 'lodash/assign';
import moduleLoader from '../moduleLoader';

const routerDataConfiger = (app, elem, attrs, scope) => new Promise(resolve => {

  app.constant('routerData', []);
  
  const allRouterData = [
    {
      name: '',
      state: 'noPermission.noPermission',
      moduleName: 'scmsModules',
    },
    {
      name: '',
      state: 'pageInclude.index',
      params: 'url',
      'inNav': 'pageInclude.index',
      moduleName: 'scmsModules',
    }
  ];

  moduleLoader()
    .then(MODULE_ENTIIES => {

      Object.keys(MODULE_ENTIIES).forEach(name => {
        const router = MODULE_ENTIIES[name].getRouterData(app, elem, attrs, scope);
        angular.forEach(router, function(item) {
          const stateObj = {
            moduleName: name,
            name: item.name,
            state: item.state,
            params: item.params,
            permissionKey: item.permissionKey,
          };
          switch (name) {
            case 'crmModules':
              assign(stateObj, {
                name2: item.name2,
                inNav: item.inNavB || item.inNav,
              });
              break;
            case 'basicConfigModules':
            case 'logSystemModules':
            case 'appPublishModules':
              assign(stateObj, {
                inNav: item.inNav,
                paramsObj: item.paramsObj,
              });
              break;
            case 'qosModules':
            case 'orderModules':
            case 'autoInsuranceModules':
            case 'marketWebModules':
            case 'kaModules':
            case 'reportWebModules':
            case 'wareHouseModules':
            case 'wisdomConnectModules':
            case 'kaWoodpeckerModules':
              assign(stateObj, {
                inNav: item.inNav,
                parentNav: item.parentNav,
                params: item.params
              });
              break;
            case 'woodpeckerModules':
              break;
          }
          allRouterData.push(stateObj);
        });
      });
      
      app.constant('allRouterData', allRouterData);

      resolve();
    });
});

module.exports = routerDataConfiger;